export default function addLikes(content) {
    console.log(content)
    $.ajax({
        url: "/api/addLikesToPersistence",
        type: "POST",
        timeout: 1000,
        data: { translation: content },
        dataType: "JSON",
        success: function (response) {
            console.log(response)
            if (response.info == 1) {
                alert("添加成功！")
            } else {
                alert("添加失败！")
            }
        },
        error: function () {
            alert("添加失败！")
        }
    });
}