<template>
  <div class="ques-wrapper" v-loading="isQuesLoading">
    <el-form
      ref="wordsForm"
      :model="formData"
      label-position="top"
      class="ques-form"
      style="max-width: 800px"
    >
      <el-form-item v-for="(content,index) in words" :key="index"
      :label="`${index+1}. ${content.word}`" 
      :prop="'content.'+index+'.translation'"
      :rules="[{pattern: regex_legal, message: '请输入合法内容', trigger: ['change','blur']}]">
        <el-input v-model="formData.content[index].translation" size="large" placeholder="请输入释义" />
      </el-form-item>

      <el-form-item size="large" class="form-btn">
        <el-button type="danger" @click="wordsForm.resetFields()">清空</el-button>
        <el-button type="primary" @click="submitForm(wordsForm)">提交</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script setup>
import { ref, reactive } from "@vue/reactivity";
import { regex_legal } from "@/assets/regex"

// 从父组件接收数据
const emit = defineEmits(["closeQues"]);
const props = defineProps(["words"]);
const wordsForm = ref("")
let formData = reactive({
  content:[
  {word:props.words[0].word,translation:""},
  {word:props.words[1].word,translation:""},
  {word:props.words[2].word,translation:""},
  {word:props.words[3].word,translation:""},
  {word:props.words[4].word,translation:""}
  ]
})

// 提交表单
const submitForm = async (form) => {
  if (!form) return
  let data = formData.content.filter(i=>i.translation != "")
  if (data.length == 0) {
    alert("空表格")
    return
  }
  await form.validate((valid, fields) => {
    if (valid) {
      isQuesLoading.value = true
      console.log('submit!')
      submitFormWord(0,data)
    } else {
      alert("含有非法内容")
      console.log('error submit!', fields)
    }
  })
}


let isQuesLoading = ref(false);

// 清空表格
// function resetForm() {
// }

let condition = [0, 0]
function submitFormWord(index, content) {
    if (index == content.length) {
        alert("成功：" + condition[1] + "个，失败：" + condition[0] + "个")
        isQuesLoading.value = false
        emit("closeQues");
    } else {
        $.ajax({
            url: "/api/submitTranslationsToTemp",
            type: "POST",
            timeout: 1000,
            data: { word: content[index].word, translation: content[index].translation },
            dataType: "JSON",
            success: function (response) {
                if (response.info == 1) {
                    condition[1]++
                    submitFormWord(index+1,content)
                } else {
                    condition[0]++
                    submitFormWord(index+1,content)
                }
            },
            error: function () {
                condition[0]++
                submitFormWord(index+1,content)
            },
        });
    }
}
