export default function subError(text, content, details) {
    console.log(text, content, details)
    $.ajax({
        url: "/api/feedback/addErratum",
        type: "POST",
        timeout: 1000,
        data: {word:text,translation:content,reason:details},
        dataType: "JSON",
        success: function (response) {
            console.log(response)
            if (response.info == 1) {
                alert("提交成功！")
            } else {
                alert("提交失败！")
            }
        },
        error: function () {
            alert("提交失败！")
        }
    })
}